.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    background: rgba(0, 0, 0, 0.75);
  }

  .modal {
    background-color: #E6FAF0;
    justify-content: center;
    align-items: center;
    margin-left: 10%;
    position:fixed;
    top: 30vh;
    left: 10%;
    border: black;
    width: 60%;
    z-index: 100;
    overflow: hidden;
  }